import React from 'react';
import PictureCard from "./cardholder/PictureCard";
import NumberCard from "./cardholder/NumberCard";

export default class CardHolder extends React.Component {
    render() {

        const CardStyle = {
            overflow: "auto",
        };

        return (
            <div style={CardStyle}>
                <table align="center" width="80%">
                    <tr align="center">
                        <td height="10%">
                            <PictureCard imageName={'/assets/images/JIRA.png'} url=
                                {"https://support.wso2.com/jira/secure/Dashboard.jspa"} marginTop={10}/>
                        </td>
                        <td height="10%">
                            <NumberCard jiraType="customer" type="Customer"/>
                        </td>
                        <td height="10%">
                            <PictureCard imageName={'/assets/images/wso2Logo.jpg'} url=
                                {"https://wso2.com"} marginTop={50}/>
                        </td>
                        <td height="10%">
                            <NumberCard jiraType="proactive" type="Proactive"/>
                        </td>
                        <td height="10%">
                            <PictureCard imageName={'/assets/images/PMT.png'} url=
                                {"https://supporttools.wso2.com/pmtapp/pages/work_log.jag"} marginTop={30}/>
                        </td>
                    </tr>
                </table>
            </div>
        );
    }
}



