import React from 'react';

class Title extends React.Component {
    render() {
        const h1Style = {
            fontSize: 50,
            margin: 5,
            color: "#000",
            fontFamily: "Helvetica",
            textAlign: "center",

            fontWeight: 100
        };
        const h2Style = {
            fontSize: 20,
            margin: 5,
            textAlign: "center",
            color: "#888888",
            fontFamily: "Helvetica",
            fontWeight: 100
        };
        const titleStyle = {
            maxWidth: "100%",
        };

        return (
            <div style={titleStyle}>
                <h1 style={h1Style}><br/>Welcome to the Patch Information Dashboard<br/></h1>
                <hr width="40%" color={"#ddd"}/>
                <p style={h2Style}><br/>WSO2 Inc.</p>
                l
            </div>
        )
    }
}

export default Title;



